:root {
  margin: 0;
  padding: 0;
  color: #333;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

.fas {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  border-style: none;
}
ul {
  list-style: none;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Pontano Sans", sans-serif;

  height: 100vh;

  background-color: white;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  /* height: 100vh; */
}

main {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: auto;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 100px;
  padding-right: 20px;
  padding-left: 20px;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
@media only screen and (min-width: 1000px) {
  main {
    padding-top: 140px;
    max-width: 1000px;
    height: 100vh;
    margin: auto;
    border-radius: 3px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}

a {
  cursor: pointer;
  color: white;
}

.loading-message {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 150px;
  margin-left: auto;
  margin-right: auto;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  font-size: 20px;
}



